import Services from '../../img/sendmoney.svg'
import Boletos from '../../img/boletos.svg'
import Compras from '../../img/compras.svg'
import Cashs from '../../img/cashback.svg'
import Cdis from '../../img/cdi.svg'


import './style.css' 

const Service = ({name}) => {
    return(
        <div className='container-service'>
            <p><a href="https://www.picpay.com/pague-e-receba-com-picpay/como-pagar">
            <img src={Services} alt='Service'/>
            </a></p>
            <p>{name}</p> 
        </div>
    )
}

const Boleto = ({name}) => {
    return(
        <div className='container-service'>
            <p><a href="https://pague-boletos.picpay.com/">
            <img src={Boletos} alt='boletos'/>
            </a></p>
            <p>{name}</p> 
        </div>
    )
}

const Compra = ({name}) => {
    return(
        <div className='container-service'>
            <p><a href="https://www.picpay.com/compre-online">
            <img src={Compras} alt='compras'/>
            </a></p>
            <p>{name}</p> 
        </div>
    )
}

const Cash = ({name}) => {
    return(
        <div className='container-service'>
            <p><a href="https://meajuda.picpay.com/hc/pt-br/articles/4406513586573-Como-posso-usar-meu-cashback-no-PicPay-">
            <img src={Cashs} alt='cashback'/>
            </a></p>
            <p>{name}</p> 
        </div>
    )
}

const Cdi = ({name}) => {
    return(
        <div className='container-service'>
            <p><a href="https://rendimentos.picpay.com/">
            <img src={Cdis} alt='cdi'/>
            </a></p>
            <p>{name}</p> 
        </div>
    )
}



export { Service , Boleto, Compra, Cash, Cdi};