import './style.css' 
import Logo from '../../img/piclogo.png'
import {Link} from 'react-router-dom'

function Header() {
  return (
    <main className='container-main'>
        <p><a><Link to = './'>
        <img src={Logo} alt='logo' onclick="window.open(this.src)"/>
        </Link>
        </a></p>
        <ul>
          <li><a><Link to = './'>Home</Link></a></li>
          <li><a><Link to = '/Services'>Serviços</Link></a></li>
          <li><a><Link to = '/Contato'>Sobre</Link></a></li>
        </ul>
    </main>
  );
}

export default Header;